<template>
    <div class="c-middle">
        <div class="h-name">
            <h3>怡乐生活馆</h3>
            <p class="name-fir">供应链管理系统</p>
            <p class="name-sec">ENTERPRISE RESOURCE PLANNING</p>
        </div>
        <template>

            <Form ref="formInline" style="margin-top: 50px">
                <FormItem>
                    <Input type="text" v-model="work_house" :readonly="true">
                    <Icon type="ios-paper-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
            </Form>
            <Form ref="formInline">
                <FormItem prop="user">
                    <Input type="text" placeholder="Username" @input="work_no()" v-model="work_user">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem prop="password">
                    <Input type="password" placeholder="Password" v-model="user_pass">
                    <Icon type="ios-locked-outline" slot="prepend"></Icon>
                    </Input>
                    <Checkbox v-model="single" style="margin-top: 10px">7天内自动登录</Checkbox>
                </FormItem>
                <FormItem style="margin-top: -10px">
                    <Button style="width: 100%" type="error" :loading="loading2" @click="encrypt()">
                        <span v-if="!loading2">Signin</span>
                        <span v-else>Loading...</span>
                    </Button>
                    <!--<Button type="info" style="width: 100%"  @click="encrypt()" >Signin</Button>-->
                </FormItem>
            </Form>
        </template>
    </div>
</template>
<script type="text/ecmascript-6">
    export default {
        name: 'Index',
        single: true,
        data () {
            return {
                work_user: '',
                user_pass: '',
                work_house: '仓库名称',
                picked: true,
                single: true,
                loading2: false
            }
        },
        methods: {

            //ceshi
            cesshi(){
                this.Axios.post('http://139.224.128.65/API/SystemSearch/InsertSearchKeyword',this.Qs.stringify({
                    Name:0,
                    CreateUserID:115
                })).then(function(res,req){
                    console.log(res.data)
                    alert('111')
                })
            },

            //密码加密
            encrypt (a) {
                var v = this;
                v.loading2 = true;
                if(v.work_user==''){
                    v.$Message.error('用户名不可为空');
                    v.loading2 = false;
                }else if(v.user_pass==''){
                    v.$Message.error('密码不可为空')
                    v.loading2 = false;
                }
                var sin = {};
                v.single === true ? sin = {expires: 7} : sin = '';
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/encryption', v.Qs.stringify({
                    text: v.user_pass
                })).then(function (res) {
                    v.Axios.post('http://116.247.103.119/imt16webapi/sheets/userlogin', v.Qs.stringify({
                        username: v.work_user,
                        userpwd: res.request.response
                    })).then(function (red) {
                        if (red.data.error == 0) {
                            v.Yl.cookie('user', JSON.stringify(red.data.data), sin);
                            v.$router.push('/List');
                        } else {
                            v.$Message.error('用户名账号匹配')
                        }
                        v.loading2 = false;
                    }).catch(function () {
                        v.loading2 = false;
                    })
                })
            },

            //根据用户名获取厂库名称
            work_no () {
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/Getbookstore_name', v.Qs.stringify({
                    work_no: v.work_user
                })).then(function (res) {
                    if (res.data.error == 0) {
                        v.work_house = res.data.data[0].bookstore_name
                    } else {
                        v.work_house = '仓库名称'
                    }
                })
            },
        },
        beforeCreate(){
        },
        mounted(){
            this.cesshi()
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import '../apull/less/Register.less';
</style>



